<template>
  <v-card class="text-center" v-if="this.loading == true">
    <v-progress-circular indeterminate color="primary"></v-progress-circular>
  </v-card>

  <v-app v-else-if="this.$store.state.isUserLoggedIn == false">
    <v-container class="orange lighten-5 fill-height">
       <Login @emit="toggleRegLog" v-if="this.login === true" />
       <Register @emit="toggleRegLog" v-if="this.login === false" />
    </v-container>
      
  </v-app>

  <v-app v-else id="one-roof-app">
    <!-- Nav drawer starts -->
    <v-navigation-drawer
      v-model="drawer"
      color="orange lighten-4"
      width="185"
      app
    >
      <!-- Navbar title start -->
      <v-list-item>
        <v-list-item-content>
          <v-list-item-title class="pt-3 font-weight-bold">
            OneRoof
          </v-list-item-title>
          <v-list-item-subtitle>
            {{this.$store.state.houseName}}
          </v-list-item-subtitle>
        </v-list-item-content>
      </v-list-item>
      <!-- Nav bar title ends -->

      <v-divider></v-divider>

      <!-- Navigation bar start -->
      <v-list dense nav>
        <v-list-item v-for="item in items" :key="item.title" :to="item.to" link>
          <v-badge
            :content="item.notifications"
            :value="item.notifications"
            color="orange darken-4"
            offset-x="40"
            offset-y="40"
            overlap
          >
            <v-list-item-icon>
              <v-icon color="brown lighten-1">{{ item.icon }}</v-icon>
            </v-list-item-icon>
          </v-badge>
          <v-list-item-content>
            <v-list-item-title>{{ item.title }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
      <!-- Navigation bar ends -->
    </v-navigation-drawer>
    <!-- Nav drawer ends -->

    <!-- App top bar start -->
    <v-app-bar app color="orange lighten-4">
      <v-container class="d-flex justify-space-between pl-0 ml-0">
        <v-badge
          :content="this.allNotifications"
          :value="this.allNotifications"
          color="red"
          offset-x="28"
          offset-y="50"
          overlap
          ><v-app-bar-nav-icon @click="drawer = !drawer" class="mr-0 pt-4 pr-6">
          </v-app-bar-nav-icon
        ></v-badge>

        <!-- <v-toolbar-title
      class="pt-3 white--text font-weight-thin"
      >
        OneRoof
      </v-toolbar-title> -->

        <v-img
          src="@/assets/OneRoof.png"
          height="50"
          width="20"
          contain
          class="mt-2 mr-3 pa-0"
        >
        </v-img>

        <router-link style="text-decoration: none" to="/profile">
          <v-icon class="pt-5" color="brown lighten-1">
            mdi-account-circle-outline
          </v-icon>
        </router-link>
      </v-container>
    </v-app-bar>
    <!-- App top bar ends -->

    <!-- Main app component -->
    <v-main class="orange lighten-5">
      <!-- Placeholder to test components -->
      <!-- Placeholder to test components -->
      <router-view></router-view>
    </v-main>
    <!-- Main app ends -->
  </v-app>
</template>


<script>
import Login from "./views/Login.vue";
import Register from "./views/Register";

export default {
  data: () => ({
    drawer: null,
    houseName: "",
    items: [
      {
        title: "Chat",
        icon: "mdi-chat",
        to: "/yourhome",
        notifications: 0,
      }, //Use to: to link views
      {
        title: "Direct Messages",
        icon: "mdi-message-text",
        to: "/dm",
        notifications: 0,
      },
      {
        title: "To-do",
        icon: "mdi-format-list-checks",
        to: "/todo",
        notifications: 0,
      },
      {
        title: "Chores",
        icon: "mdi-clipboard-edit",
        to: "/chores",
        notifications: 0,
      },
    ],
    allNotifications: 0,
    loggedInFlag: false,
    login: true,
    loading: true,
  }),
  name: "App",
  components: {
    Login,
    Register,
  },
  methods: {
    toggleRegLog() {
      this.login = !this.login;
    },
  },
  async mounted() {
    console.time("app mounting");
    this.loading = true;
    let loggedInFlag = await this.$store.dispatch("checkIfLoggedInUser");
    if (loggedInFlag !== false) {
      console.log("MOUNTING USER:", this.$store.state.user);
      let username = this.$store.state.user.username;
      let house_key = this.$store.state.user.house_keys[0];
      if (username) {
        await this.$store.dispatch("calculateUserRGB", username);
      }
      await this.$store.dispatch("getTodos", house_key);
      await this.$store.dispatch("populateVictimList", house_key);
      console.log("MOUNTING TODOS AND VICTIMS:", this.$store.state.todos);
      await this.$store.dispatch("getChores", house_key);
      console.log("MOUNTING CHORES:", this.$store.state.chores);
      // await this.$store.dispatch("getHouseName", this.$store.state.user.house_keys[0]);
      // this.houseName = this.$store.user.houseName;
      //await get all messages for general chat
      this.allNotifications = 0;
      this.items[1].notifications = this.$store.state.userTodoNotifications;
      for (let item of this.items) {
        this.allNotifications += item.notifications;
      }
    }
    console.timeEnd("app mounting");
    this.loading = false;  
  },
  computed: {
    countNotifications() {
      return this.$store.state.userTodoNotifications;
    },
  },
  watch: {
    countNotifications(newCount) {
      this.items[2].notifications = newCount;
      this.allNotifications = 0;
      for (let item of this.items) {
        this.allNotifications += item.notifications;
      }
    },
  },
};
</script>

<style>
body {
  background-color: tan;
}
</style>
